<template><section><h1>GroundPrimitive 图元</h1>
<p><code v-pre>vc-primitive-ground</code> 组件用于加载贴地图元，通过它加载贴地或 3DTiles 的几何图形，支持的几何图形有：<code v-pre>CircleGeometry</code>、 <code v-pre>CorridorGeometry</code>、 <code v-pre>EllipseGeometry</code>、 <code v-pre>PolygonGeometry</code>、<code v-pre>RectangleGeometry</code>。</p>
<h2>示例</h2>
<h3>加载贴地图元</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-provider-terrain-cesium></vc-provider-terrain-cesium>
        <vc-primitive-ground :appearance="appearance" :asynchronous="asynchronous" :interleave="interleave">
          <vc-instance-geometry :geometry="geometry">
            <vc-geometry-rectangle :rectangle="rectangle"></vc-geometry-rectangle>
          </vc-instance-geometry>
        </vc-primitive-ground>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
