      <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-terrain-cesium</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-terrain-cesium</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive-ground</span> <span class="hljs-attr">:appearance</span>=<span class="hljs-string">"appearance"</span> <span class="hljs-attr">:asynchronous</span>=<span class="hljs-string">"asynchronous"</span> <span class="hljs-attr">:interleave</span>=<span class="hljs-string">"interleave"</span>&gt;</span>
