        <span class="hljs-attr">geometry</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">image</span>: <span class="hljs-string">'https://zouyaoji.top/vue-cesium/statics/SampleData/radarImage/1.png'</span>,
        <span class="hljs-attr">rectangle</span>: { <span class="hljs-attr">west</span>: <span class="hljs-number">102.5</span>, <span class="hljs-attr">south</span>: <span class="hljs-number">29.5</span>, <span class="hljs-attr">east</span>: <span class="hljs-number">106.5</span>, <span class="hljs-attr">north</span>: <span class="hljs-number">33.5</span> },
        <span class="hljs-attr">interleave</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">asynchronous</span>: <span class="hljs-literal">false</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
        <span class="hljs-keyword">const</span> { Cesium, viewer } = <span class="hljs-keyword">this</span>.cesiumInstance
        viewer.camera.setView({
          <span class="hljs-attr">destination</span>: <span class="hljs-keyword">new</span> Cesium.Cartesian3(<span class="hljs-number">-1432246.8223880068</span>, <span class="hljs-number">5761224.588247942</span>, <span class="hljs-number">3297281.1889481535</span>),
