              <span class="hljs-attr">type</span>: <span class="hljs-string">'Image'</span>,
              <span class="hljs-attr">uniforms</span>: {
                <span class="hljs-attr">image</span>: <span class="hljs-keyword">this</span>.image
              }
            }
          })
        })
