      },
      play() {
        <span class="hljs-keyword">let</span> urls = [
          <span class="hljs-string">'https://zouyaoji.top/vue-cesium/statics/SampleData/radarImage/1.png'</span>,
          <span class="hljs-string">'https://zouyaoji.top/vue-cesium/statics/SampleData/radarImage/2.png'</span>,
          <span class="hljs-string">'https://zouyaoji.top/vue-cesium/statics/SampleData/radarImage/3.png'</span>,
