<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>geometryInstances</td>
<td>Object|Array</td>
<td></td>
<td><code v-pre>optional</code> 指定图元用于渲染的几何图形实例。</td>
</tr>
<tr>
<td>appearance</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定图元的外观参数，如果具有 color 属性，默认为 PerInstanceColorAppearance 。</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定图元是否显示。</td>
</tr>
<tr>
<td>vertexCacheOptimize</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定是否优化几何体顶点着色器之前和之后的缓存。</td>
</tr>
<tr>
<td>interleave</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定是否交错几何体顶点属性，true 时可以稍微改善渲染性能，但会增加加载时间。</td>
</tr>
<tr>
<td>compressVertices</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定是否压缩几何体顶点，压缩可以以节省内存。</td>
</tr>
<tr>
<td>releaseGeometryInstances</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定是否保留图元对几何体实例的输入，不保留可以节省内存。</td>
</tr>
<tr>
<td>allowPicking</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定图元是否可以被 Scene.pick 拾取，关闭拾取可以节省内存。</td>
</tr>
<tr>
<td>asynchronous</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定图元时异步加载还是同步加载。</td>
</tr>
<tr>
<td>classificationType</td>
<td>Number</td>
<td><code v-pre>2</code></td>
<td><code v-pre>optional</code> 指定是贴地形还是贴 3DTiles，还是两者都贴。 <strong>TERRAIN: 0, CESIUM_3D_TILE: 1, BOTH: 2</strong></td>
</tr>
<tr>
<td>debugShowBoundingVolume</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定是否显示图元的边界球，用于调试使用。</td>
</tr>
<tr>
<td>debugShowShadowVolume</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定是否绘制图元中每个几何图形的阴影体积，用于调试使用。</td>
</tr>
</tbody>
</table>
<hr>
<p>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/GroundPrimitive.html">GroundPrimitive</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该图元上按下时触发。</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标在该图元上弹起时触发。</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标单击该图元时触发。</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标左键双击该图元时触发。</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>鼠标移动到该图元时触发。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          geometry: null,
          image: 'https://zouyaoji.top/vue-cesium/statics/SampleData/radarImage/1.png',
          rectangle: { west: 102.5, south: 29.5, east: 106.5, north: 33.5 },
          interleave: true,
          asynchronous: false
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const { Cesium, viewer } = this.cesiumInstance
          viewer.camera.setView({
            destination: new Cesium.Cartesian3(-1432246.8223880068, 5761224.588247942, 3297281.1889481535),
            orientation: {
              heading: 6.20312220367255,
              pitch: -0.9937536846355606,
              roll: 0.002443376981836387
            }
          })
          this.appearance = new Cesium.MaterialAppearance({
            material: new Cesium.Material({
              fabric: {
                type: 'Image',
                uniforms: {
                  image: this.image
                }
              }
            })
          })
          this.play()
        },
        play() {
          let urls = [
            'https://zouyaoji.top/vue-cesium/statics/SampleData/radarImage/1.png',
            'https://zouyaoji.top/vue-cesium/statics/SampleData/radarImage/2.png',
            'https://zouyaoji.top/vue-cesium/statics/SampleData/radarImage/3.png',
            'https://zouyaoji.top/vue-cesium/statics/SampleData/radarImage/4.png',
            'https://zouyaoji.top/vue-cesium/statics/SampleData/radarImage/5.png'
          ]
          let i = 0
          let _this = this
          setInterval(function() {
            _this.appearance.material.uniforms.image = urls[i]
            i++
            if (i === 5) i = 0
          }, 500)
        }
      }
    }
  </script>